}

@media(max-width: 576px) {
  .content {
    font-size: 18px;
    line-height: 2em;
  }
  .top-left {
    font-size: 20px;
  }
  .container {
    padding-top: 20px;
  }
  .landing-image {
    border: 3px solid black;
    width: 150px
  }
  .kategori {
    padding-top: 200px;
  }
  .navbar-nav {
    text-align: left;
  }
  .navbar.navbar-expand-lg.navbar-light {
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media(max-width: 768px) {
  .full-name {
    padding-top: 20px;
    font-size: 22px;
    text-align: center;
  }
  .image-boundary {
